e-space: nowrap;\n    vertical-align: middle;\n    padding: 0.6rem 1rem;\n    border-radius: 2px;\n    -webkit-transition: all 0.2s ease-in-out;\n    transition: all 0.2s ease-in-out;\n    cursor: pointer;\n    min-height: 38px;\n}\n\nbutton.default {\n    background-color: #e8e8e8;\n    color: #333;\n    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.12);\n}\n\nbutton.primary {\n    background-color: #25be38;\n    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.12);\n    color: #fff;\n}\n\nbutton.accent {\n    background-color: #1778dd;\n    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.12);\n    color: #fff;\n}\n\n#username-page {\n    text-align: center;\n}\n\n#create-account-page {\n    text-align: center;\n}\n\n.username-page-container {\n    background: #fff;\n    box-shadow: 0 1px 11px rgba(0, 0, 0, 0.27);\n    border-radius: 2px;\n    width: 100%;\n    max-width: 500px;\n    display: inline-block;\n    margin-top: 42px;\n    vertical-align: middle;\n    position: relative;\n    padding: 35px 55px 35px;\n    min-height: 250px;\n    position: absolute;\n    top: 50%;\n    left: 0;\n    right: 0;\n    margin: 0 auto;\n    margin-top: -160px;\n}\n\n.username-page-container .username-submit {\n    margin-top: 10px;\n}\n\n#chat-page {\n    position: relative;\n    height: 100%;\n}\n\n.chat-container {\n    max-width: 700px;\n    margin-left: auto;\n    margin-right: auto;\n    background-color: #fff;\n    box-shadow: 0 1px 11px rgba(0, 0, 0, 0.27);\n    margin-top: 30px;\n    height: calc(100% - 60px);\n    max-height: 600px;\n    position: relative;\n}\n\n#chat-page ul {\n    list-style-type: none;\n    background-color: #FFF;\n    margin: 0;\n    overflow: auto;\n    overflow-y: scroll;\n    padding: 0 20px 0px 20px;\n    height: calc(100% - 150px);\n}\n\n#chat-page #messageForm {\n    padding: 20px;\n}\n\n#chat-page ul li {\n    line-height: 1.5rem;\n    padding: 10px 20px;\n    margin: 0;\n    border-bottom: 1px solid #f4f4f4;\n}\n\n#chat-page ul li p {\n    margin: 0;\n}\n\n#chat-page .event-message {\n    width: 100%;\n    text-align: center;\n    clear: both;\n}\n\n#chat-page .event-message p {\n    color: #777;\n    font-size: 14px;\n    word-wrap: break-word;\n}\n\n#chat-page .chat-message {\n    padding-left: 68px;\n    position: relative;\n}\n\n#chat-page .chat-message i {\n    position: absolute;\n    width: 42px;\n    height: 42px;\n    overflow: hidden;\n    left: 10px;\n    display: inline-block;\n    vertical-align: middle;\n    font-size: 18px;\n    line-height: 42px;\n    color: #fff;\n    text-align: center;\n    border-radius: 50%;\n    font-style: normal;\n    text-transform: uppercase;\n}\n\n#chat-page .chat-message span {\n    color: #333;\n    font-weight: 600;\n}\n\n#chat-page .chat-message time {\n    color: #777;\n    margin-left: 15px;\n    font-size: 14px;\n}\n\n#chat-page .chat-message p {\n    color: #43464b;\n}\n\n#messageForm .input-group input {\n    float: left;\n    width: calc(100% - 85px);\n}\n\n#messageForm .input-group button {\n    float: left;\n    width: 80px;\n    height: 38px;\n    margin-left: 5px;\n}\n\n.chat-header {\n    text-align: center;\n    padding: 15px;\n    border-bottom: 1px solid #ececec;\n}\n\n.chat-header h2 {\n    margin: 0;\n    font-weight: 500;\n}\n\n.connecting {\n    padding-top: 5px;\n    text-align: center;\n    color: #777;\n    position: absolute;\n    top: 65px;\n    width: 100%;\n}\n\n@media screen and (max-width: 730px) {\n\n    .chat-container {\n        margin-left: 10px;\n        margin-right: 10px;\n        margin-top: 10px;\n    }\n}\n\n@media screen and (max-width: 480px) {\n    .chat-container {\n        height: calc(100% - 30px);\n    }\n\n    .username-page-container {\n        width: auto;\n        margin-left: 15px;\n        margin-right: 15px;\n        padding: 25px;\n    }\n\n    #chat-page ul {\n        height: calc(100% - 120px);\n    }\n\n    #messageForm .input-group button {\n        width: 65px;\n    }\n\n    #messageForm .input-group input {\n        width: calc(100% - 70px);\n    }\n\n    .chat-header {\n        padding: 10px;\n    }\n\n    .connecting {\n        top: 60px;\n    }\n\n    .chat-header h2 {\n        font-size: 1.1em;\n    }\n}\n/*# sourceMappingURL=data:application/json;base64, */"